import { Component, OnInit } from '@angular/core';

// Ionic
import { NavController } from '@ionic/angular';

// Services
import { DatabaseService } from '../../services/database.service';
import { StorageService } from '../../services/storage.service';

// Moment
import * as moment from 'moment';  
@Component({
  selector: 'app-request-list',
  templateUrl: './request-list.page.html',
  styleUrls: ['./request-list.page.scss'],
})
export class RequestListPage implements OnInit {
  is_loading: boolean = true;
  services: any;
  requests: any [];
  constructor(private database: DatabaseService,
              private navCtrl: NavController,
              private storage: StorageService) { }

  ngOnInit() {
    this.storage.getValue ('user_uid').then (user_id => {
      this.database.getRequestsToConfirmByUser (user_id).subscribe (response => {
        this.requests = response;
        this.is_loading = false;

        console.log (response);
      });
    });
  }

  getRelativeDateTime (date: string) {
    return moment (date, "").fromNow ();
  }

  viewRequest (item: any) {
    this.navCtrl.navigateForward ('request-pro-view/' + item.id);
  }
}
